]

// 页面数据加载
onLoad(async (options) => {
  if (options.type) {
    if (options.type == 1) {
      category_list.value = offeringTestList.map((item) => ({
        ...options,
        id: item.id,
        name: item.name,
        image: item.image,
        page: "/pages/chooseOffering/from",
      }))
    } else {
      category_list.value = requestTestList.map((item) => ({
        ...options,
        id: item.id,
        name: item.name,
        image: item.image,
        page: "/pages/chooseOffering/from",
      }))
    }
  }

